
.container{
  max-width:1000px;
  padding:10px 50px 0 50px;
}
.detail {
    position: relative;
    max-width: 800px;
    color: #666;
}
.backBtn {
    width: 90px;
    color: #035C6E;
    line-height: 30px;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 50;
}
.lineBox {
    width: 30px;
    height: 30px;
    position:relative;
    float: right;
}
.lineBox div {
    transition: 600ms;
    width: 8px;
    height: 8px;
    margin: 1px;
    background: #7B421B;
    position: relative;
    float: left;
    opacity: 0;
    transform: scale(2);
    -webkit-transform: scale(2);
}
.backBtnShow .lineBox div {
    opacity: 1;
    transform: scale(1);
    -webkit-transform: scale(1);
    background: #a5a5a5;
}
.lineBox div:nth-child(1) {
    left: -10px;
    top: -10px;
}
.lineBox div:nth-child(2) {
    left: 0;
    top: -10px;
}
.lineBox div:nth-child(3) {
    left: 10px;
    top: -10px;
}
.lineBox div:nth-child(4) {
    left: -10px;
    top: 0;
}
.lineBox div:nth-child(5) {
    left: 0;
    top: 0;
}
.lineBox div:nth-child(6) {
    left: 10px;
    top: 0;
}
.lineBox div:nth-child(7) {
    left: -10px;
    top: 10px;
}
.lineBox div:nth-child(8) {
    left: 0;
    top: 10px;
}
.lineBox div:nth-child(9) {
    left: 10px;
    top: 10px;
}
.backBtn span {
    transition: 600ms 700ms;
    width: 60px;
    display: inline-block;
    opacity: 0;
    color: #a5a5a5;
    position: absolute;
    left: -20px;
    top: 0;
}
.backBtnShow span {
    left: 0px;
    opacity: 1;
}
.backBtn:hover .lineBox {
    transform: rotate(90deg) scale(0.5);
}
.backBtn:hover .lineBox div {
    background: #035C6E;
}
.backBtn:hover span {
    left: 5px;
    color: #035C6E;
}
.backBtn:hover .lineBox div:nth-child(2) {
    left: 20px !important;
    top: -10px !important;
}
.backBtn:hover .lineBox div:nth-child(4) {
    left: -10px !important;
    top: -20px !important;
}
.backBtn:hover .lineBox div:nth-child(6) {
    left: 10px !important;
    top: 20px !important;
}
.backBtn:hover .lineBox div:nth-child(8) {
    left: -20px !important;
    top: 10px !important;
}
.detail .title {
    font-size: 24px;
    text-align: center;
    line-height: 30px;
    margin: 20px 0;
}
.detail .info {
    text-align: center;
    font-size: 12px;
    color: #999;
}
.detail .cont {
    margin: 20px 0 0 0;
    font-size: 14px;
    line-height: 24px;
}
.detail .cont img {
    max-width: 100%;
    display: block;
}




